.experience {
  display: inline-block;
  margin: 10px;
  text-align: center;
  padding-left: 5%;
  padding-right: 5%;
}

.exp-photo {
  width: 270px;
  height: 180px;
  border-radius: 13px;
}

.beep{
  font-size: 13px;
  text-align: center;
}
.textWithBlurredBg{
  width:300px;
  height:auto;
  display:inline-block;
  position:relative;
  transition:.3s;
  margin:4px;
}

.textWithBlurredBg img{
  /* width:100%;
  height:100%; */
  transition:.3s;
  border-radius:4px;
}

.textWithBlurredBg:hover img{
  filter:blur(2px) brightness(60%);
  box-shadow:0 0 16px cyan;
}
 
.textWithBlurredBg :not(img){
  position:absolute;
  z-index:1;
  top:17%;
  width:100%;
  text-align:center;
  color:#fff;
  opacity:0;
  transition:.3s;
  font-size: 25px;
}

.textWithBlurredBg h3{
  top:50%
}

.textWithBlurredBg:hover :not(img){
  opacity:1;
}